import React, {useState} from 'react'
import Layout from '../core/Layout'
import { isAuthenticated } from '../auth/index'
import { Link } from 'react-router-dom'
import {createCategory} from './ApiAdmin'

const AddCategory = () => {
    const[name, setName] = useState('')

    const[error, setError] = useState(false)

    const[success, setSuccess] = useState(false)

    // destructure user and info from localstorage

    const {user, token} = isAuthenticated()

    const handleChange = e => {
        setError('')
        setName(e.target.value)
    }

    const clickSubmit = e => {
        e.preventDefault();
        setError("");
        setSuccess(false);
        //make request to api to create category
        createCategory(user._id, token, {name})
        .then(data => {
            console.log(data.error)
            if (data.error) {
                setError(true);
            }else {
                setError('');
                setSuccess(true);
            }
        })
    }

    const newCategoryForm = () => (
        <form onSubmit={clickSubmit} className="text-center">
            <div className="form-group text-left">
                <label className="text-muted">Name</label>
                <input type="text" className="form-control" onChange={handleChange} value={name} autoFocus required/>
            </div>
                <button className="btn btn-outline-primary">Create Category</button>
        </form>
    );

    const showSuccess = () => {
        if(success){
            return <h3 className="text-success">Category <b>{name}</b> is created</h3>
        }
    }

    const showError = () => {
        if(error){
            return <h3 className="text-danger">Category should be unique</h3>
        }
    }

    const goBack = () => (
        <div className="mt-5">
            <Link to="/admin/dashboard" className="text-warning" >Back to Dashboard</Link>
        </div>
    )

    return(
        <Layout title="Add a new category" description= {`Hello ${user.name}, You can create new product categories here.`}>
            <div className="row">
                <div className="col-md-6 offset-md-3">
                    {showError()}
                    {showSuccess()}
                    {newCategoryForm()}
                    {goBack()}
                </div>
            </div>            
        </Layout>
    )
}

export default AddCategory;