import React, { Fragment } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { signout, isAuthenticated } from '../auth/index'
import { itemTotal } from "./cartHelpers";

const isActive = (history, path) => {
    if (history.location.pathname === path) {
        return { backgroundColor: '#ffffff', color: '#343A40', borderRadius: '10px' }
    }
    else {
        return { color: '#ffffff', borderRadius: '10px' }
    }
}

const Menu = ({ history }) => (
    <div>
        <nav className="navbar navbar-expand-sm bg-dark navbar-dark d-flex justify-content-around" style={{flexDirection: "row"}}>
            <a className="navbar-brand" href="/" style={{fontFamily: 'monospace', fontWeight: 'bold', fontSize: 30}}>
                <img src="https://i.imgur.com/Is2E9Zk.png" alt="Guten Shop" width="200"/>
            </a>
            <ul className="nav nav-tabs d-flex justify-content-around" style={{borderBottom: 0, marginLeft: 'auto', fontSize: 20}} >
                <li className="nav-item">
                    <Link className="nav-link" style={isActive(history, '/')} to="/">Home</Link>
                </li>

                <li className="nav-item">
                    <Link className="nav-link" style={isActive(history, '/shop')} to="/shop">Shop</Link>
                </li>
                
                <li className="nav-item">
                <Link
                    className="nav-link"
                    style={isActive(history, "/cart")}
                    to="/cart"
                >
                    Cart{" "}
                    <sup>
                        <small className="cart-badge">{itemTotal()}</small>
                    </sup>
                </Link>
                </li>

                {isAuthenticated() && isAuthenticated().user.role === 0 && (
                    <li className="nav-item">
                        <Link className="nav-link" style={isActive(history, '/user/dashboard')} to="/user/dashboard">Dashboard</Link>
                    </li>
                )}

                {isAuthenticated() && isAuthenticated().user.role === 1 && (
                    <li className="nav-item">
                        <Link className="nav-link" style={isActive(history, '/admin/dashboard')} to="/admin/dashboard">Admin Dashboard</Link>
                    </li>
                )}

                {!isAuthenticated() && (
                    <Fragment>
                        <li className="nav-item">
                            <Link className="nav-link" style={isActive(history, '/signin')} to="/signin">SignIn</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" style={isActive(history, '/signup')} to="/signup">SignUp</Link>
                        </li>
                    </Fragment>
                )}
                {isAuthenticated() && (
                    <li className="nav-item">
                        <span className="nav-link" style={{ cursor: 'pointer', color: '#ffffff', borderRadius: '10px' }} onClick={() => signout(() => {
                            history.push('/')
                        })}>Sign Out</span>
                    </li>
                )}
            </ul>
        </nav>
    </div>
)

export default withRouter(Menu);